// -----------------------------------------------------------------------------
// This file contains very basic styles.
// -----------------------------------------------------------------------------

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    //This defines what 1rem is
    font-size: 62.5%;
}

body {
    box-sizing: border-box;
    padding: 2rem;
}
 
::selection {
    background-color: $color-blue-light;
    color: $white;
}